import React, { useState, useEffect } from 'react';
import mqtt from 'mqtt';

interface LockStatusProps {
  isLocked: boolean;
}

const LockStatus: React.FC<LockStatusProps> = ({ isLocked }) => {
  const [status, setStatus] = useState(isLocked);

  useEffect(() => {
    // MQTTクライアントの設定を読み込む
    const config = require('../config/mqtt.config.json');
    const client = mqtt.connect(config.brokerUrl);
    const topic = config.topic;

    client.on('connect', () => {
      console.log('MQTT接続成功');
      client.subscribe(topic);
    });

    client.on('message', (receivedTopic, message) => {
      if (receivedTopic === topic) {
        const lockStatus = message.toString() === 'locked';
        setStatus(lockStatus);
      }
    });

    // クリーンアップ関数
    return () => {
      client.end();
    };
  }, []); 

  return (
    <div className="lock-status">
      <div className={`status-indicator ${status ? 'locked' : 'unlocked'}`}>
        {status ? '施錠中' : '解錠中'}
      </div>
    </div>
  );
};

export default LockStatus;